import { Col, Input, Row } from 'antd';
import React, { useState } from 'react';
import logo from '../../../assets/logo.png';
import fb from '../../../assets/Icons/facebook.png';
import li from '../../../assets/Icons/linkedin.png';
import gh from '../../../assets/Icons/github.png';
import ig from '../../../assets/Icons/instagram.png';
import tt from '../../../assets/Icons/twitter.png';
import { PushpinFilled, PhoneFilled, MailFilled } from '@ant-design/icons';
const Footer = () => {
  const [mailText, setMailText] = useState();
  return (
    <div className='footer'>
      <div className='footer-container '>
        <Row gutter={12}>
          <Col md={10}>
            <div className='left-div'>
              <img src={logo} alt='ginnovation' className='logo img-fluid' />
              <p>
                Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                Consectetur consequuntur
              </p>
              <p className='copyright'>© 2020 All copyright reserved</p>
              <div className='sm-container'>
                <a href='#' target='_blank' rel='noreferrer'>
                  <img src={li} alt='social media' />
                </a>
                <a href='#' target='_blank' rel='noreferrer'>
                  <img src={gh} alt='social media' />
                </a>
                <a href='#' target='_blank' rel='noreferrer'>
                  <img src={ig} alt='social media' />
                </a>
                <a href='#' target='_blank' rel='noreferrer'>
                  <img src={tt} alt='social media' />
                </a>

                <a href='#' target='_blank' rel='noreferrer'>
                  <img src={fb} alt='social media' />
                </a>
              </div>
            </div>
          </Col>
          <Col md={2}>
            <div className='design'></div>
          </Col>
          <Col md={6}>
            <div className='contact'>
              <h3>Contact</h3>
              <ul>
                <li>
                  <a href='#'>
                    <PushpinFilled className='icon' />
                    Ginnovatio,ECE Dept,GIETU,Gunupur
                  </a>
                </li>
                <li>
                  <a href='#'>
                    <PhoneFilled className='icon' />
                    (86) 266 4520
                  </a>
                </li>
                <li>
                  <a href='#'>
                    <MailFilled className='icon' />
                    ginnovation@giet.edu
                  </a>
                </li>
              </ul>
            </div>
          </Col>
          <Col md={6}>
            <div className='mail'>
              <h3>Mail Us</h3>
              <form
                action='mailto:mentor.ginnovation@gmail.com'
                method='post'
                enctype='text/plain'
              >
                <input
                  name='Your Message'
                  type='text'
                  value={mailText}
                  onChange={(e) => {
                    setMailText(e.target.value);
                  }}
                />
                <button type='submit' value='Send'>
                  Send Mail
                </button>
              </form>
            </div>
          </Col>
        </Row>
      </div>
      <div className='developer'>
        <div>
          <p>Crafted with 🤍 by Team Ginnovation</p>
        </div>
        <div>
          <p className='website'>www.ginnovation.club</p>
        </div>
      </div>
    </div>
  );
};

export default Footer;
